
.news-section {
  background-color:#333;
  padding: 20px;
}

.news-image {
  width: 100%;
  max-height: 100px;
  object-fit: stretch; /* This will make sure the image covers the container without distortion */
  margin-bottom: 15px; /* Space between image and title */
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.news-container {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.news-item p {
  margin: 20px;
  color: #ffffff;
}

.news-item {
  width: 100%;
  width: 600px;
  height: 280px;
  padding: 0px;
  border-radius: 10px;
  background-color: #282828;
}

.arrow-container {
  display: flex;
  justify-content: flex-end;
  margin: 10px;
  text-decoration: none;
}

.arrow-circle {
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #000000;
  color: #0099cc;
  font-size: 32px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

.arrow-circle:hover {
  background-color: #0099cc3d;
}
