.terms h1 {
    text-align: center;
}

.terms-main {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-image: url('SubtleBckg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.terms {
    font-size: 15px;
    background-color: #333333be;
    max-width: 800px;
    padding-top: 22px;
    padding-bottom: 20px;
    text-align: left;
    color: white;
    padding-left: 80px;
    padding-right: 80px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow for depth */
}