.news-details {
  background-color: #333;
    max-width: 100%;
    height: 100%;
    text-align: left;
    padding-top: 20px;
    padding-bottom: 200px;
  }
  
  .news-details h1 {
    margin: 20px;
    font-size: 2rem;
    color: #ffffff;
  }
  
  .news-details p {
    margin: 20px;
    font-size: 1.1rem;
    color: #ffffff;
    line-height: 1.6;
  }

  .news-details ul {
    margin: 20px;
    list-style-type: disc;
    color: #ffffff;
    padding-left: 20px;
  }
  
  .news-details li {
    margin: 20px;
    color:aqua;
    margin-bottom: 5px;
  }